@import url(https://fonts.googleapis.com/css2?family=Open+Sans);
@import url(https://fonts.googleapis.com/css2?family=Do+Hyeon&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mukta&display=swap);
body {
    background: #191C24;
}

* {
    margin: 0;
    padding: 0;
}
.banner_wrapper {
    background-color: #191C24;
    overflow: hidden;
}
.icon {
    display: block;
    margin-left: auto;
    margin-right: auto;  
    width: 150px;
    max-width: 100%;
    height: auto;
    border-radius: 100px;
    opacity: 0.8;
    border-style: solid;
    border-color: whitesmoke;
}

.separator {
    width: 50vw;
    background-color: #AFD2E9; 
    height: 1px; 
    border: 0;
    margin: auto;
}

.about {
    text-align: center;
}

.title {
    font-family: 'Nunito', sans-serif;
    font-size: 10vh;
    color:#FCCA46;
    font-weight: 500;
}

.text {
    font-family: 'Open Sans', sans-serif;
    color: whitesmoke;
    font-size: 2.5vh;
    font-weight: 0;
    margin: 30px 25vw 30px 25vw;
}

.button_banner {
    margin: 20px;
    background-color: #191C24;
    border: solid;
    color: white;
    padding: 15px 10px;
    text-align: center;
    font-size: 20px;
    font-family: 'Courier New', Courier, monospace;
    border-width: 2px;
    cursor: pointer;
    outline: none;
}

.button_banner:hover {
    background-color: #FCCA46;
    border: none;
    padding: 17px 10px;
}

.button_banner a {
    text-decoration: none;
    color: white;
}

.button_banner a:active {
    text-decoration: none;
}
.projects {
    background-color: #252934;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
}

.projects_gallery {
    margin: auto;
}

.projects_title {
    color: #707B99;
    font-family: 'Raleway';
    font-size: 60px;
    text-align: center;
    margin: auto;
    margin-bottom: 50px;
}



.sidebar__list {
    display: flex;
    flex-flow: column wrap;
    justify-content: space-evenly;
    height: 100vh;
    width: 100px;
    background-color: #191C24;
    align-items: center;
    list-style: none;
}

.sidebar__icon {
    color: whitesmoke;
    font-size: 40px;
    opacity: 0.8;
}

.sidebar__icon:hover {
    color: #FCCA46;
    opacity: 1.0;
}

@media all and (max-width: 700px) {
    .sidebar__list {
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-around;
        height: 70px;
        width: 100vw;
    }
}


.gallery {
    display: flex;
    flex-wrap: wrap;
}


@media (max-width: 500px) {
    .gallery {
        display: flex;
        flex-direction: column;
    }
}

.item {
    position: relative;
    margin: 0;
    cursor: pointer;
}

.item_img {
    display: block;
    opacity: 0.8;
}

.item_img--state-hover {
    display: block;
    opacity: 0.05;
    transition: .8s;
}

.item_description,
.item_description--state-hover {
    font-size: 40px;
    position: absolute;
    left: 0;
    margin-top: 20px;
    top: 0;
    text-align: center;
    visibility: hidden;
}

.item_description--state-hover {
    visibility: visible;
}

.item_text {
    color: whitesmoke;
    font-family: 'Nunito';
    font-size: 20px;
    text-align: center;
}

.item_subtext {
    color: whitesmoke;
    font-family: 'Nunito';
    font-size: 15px;
    margin-top: 5px;
    text-align: center;
}

.item_technologies {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
}

.item_button {
    background-color: #252934;
    border: 2px solid whitesmoke;
    color: white;
    padding: 5px 10px;
    text-align: center;
    font-size: 15px;
    cursor: pointer;
    outline: none;
    text-align: center;
    opacity: 0.6;
}

.item_button:hover {
    border: 2px solid whitesmoke;
    opacity: 0.9;
}

.item_button a {
    text-decoration: none;
    color: white;
}

.item_button a:active {
    text-decoration: none;
}
.badge {
    background-color: #08A045;
    border-radius: 10px;
    width: 50px;
    height: 20px;
    line-height: 20px;
    padding-left: 5px;
    padding-right: 5px;
    text-align: center;
    margin-left: 10px;
}

.badge_text {
    font-size: 15px;
    font-family: 'Mukta';
    color: whitesmoke;
}
.contact {
    background-color: #252934;
    display: flex;
    flex-flow: row wrap;
}

.contact_form {
    margin: auto;
}

.contact_title {
    color: #707B99;
    font-family: 'Raleway';
    font-size: 20px;
    text-align: center;
    margin: auto;
    margin-bottom: 50px;
}

.contact_separator {
    width: 150px;
    margin: 10px auto 20px auto;
    background-color: whitesmoke;
    height: 1px;
    border: none;
    opacity: 0.5;
}

.contact_field {
    display: flex;
    flex-direction: row;
    width: 100%;
}

.contact_input {
    flex: 1 1 auto;
    width: 250px;
    height: 35px;
    background-color: #3f465a;
    opacity: 0.5;
    border: none;
    margin-left: 5px;
    margin-top: 8px;
    font-family: 'Open Sans', sans-serif;
    font-size: 15px;
    color: whitesmoke;
    padding-left: 5px;
}

.contact_field textarea {
    height: 150px;
    resize: none;
    overflow: hidden;
}

.contact_input::-webkit-input-placeholder {
    color: whitesmoke;
}

.contact_input:-ms-input-placeholder {
    color: whitesmoke;
}

.contact_input::-ms-input-placeholder {
    color: whitesmoke;
}

.contact_input::placeholder {
    color: whitesmoke;
}

.contact_button {
    background-color: #323847;
    border:none;
    border-radius: 5px;
    color: whitesmoke;
    display: flex;
    flex-direction: row wrap;
    font-size: 15px;
    justify-content: space-around;
    margin-left: auto;
    margin-top: 10px;
    opacity: 0.8;
    padding: 5px 10px;
    text-align: start;
    text-decoration: none;
    transition-duration: 0.4s;
    width: 70px;
}

.contact_button:hover {
    background-color: #FCCA46;
    border: none;
    color: #191c24;
    opacity: 0.9;
}

@media (max-width: 500px) {
    .contact {
        display: flex;
        flex-flow: column wrap;
        height: 100vh;
    }

    .contact_field {
        display: flex;
        flex-flow: column wrap;
    }

    .contact_button {
        margin-left: 60%;
    }
}

